<template>
    <div>
        <van-cell-group>
            <van-field label-align="right" label="房屋地址："  />
            <van-field label-align="right" label="类别：" :value="location" readonly placeholder="点击选择" @click-input="showPicker=true" :right-icon="location ? 'close' : ''" @click-right-icon="location=''" />
            <van-field label-align="right" label="低保证号："  />
            <van-field label-align="right" label="月收入：" type="number"  />
            <van-field label-align="right" label="银行卡号：" type="number" :formatter="formatterFn" v-model="cardNo" maxlength="23" />
            <van-field label-align="right" label="开户银行："  />
            <van-field label="上传社保照片：" label-width="10em" :border="false" readonly></van-field>
            <div style="margin-left: 4.26667vw">
                <Upload></Upload>
            </div>
        </van-cell-group>
        <div class="submit-block"><van-button type="primary" block>提交结果</van-button></div>
        <van-popup v-model="showPicker" round position="bottom">
            <van-picker
                show-toolbar
                :columns="columns"
                @cancel="showPicker = false"
                @confirm="onConfirm"
            />
        </van-popup>
    </div>
    
</template>
<script>
import Upload from '../../componets/upload.vue'
import { CellGroup, Field, Uploader, Button, Popup, Picker  } from 'vant'
export default {
    name: 'repairDetail',
    components: {
        [CellGroup.name]: CellGroup,
        [Field.name]: Field,
        [Uploader.name]: Uploader,
        [Button.name]: Button,
        [Popup.name]: Popup,
        [Picker.name]: Picker,
        Upload
    },
    data() {
        return {
            location: '',
            cardNo: '',
            fileList: [],
            showPicker: false,
            columns: ['杭州', '宁波', '温州', '绍兴', '湖州', '嘉兴', '金华', '衢州']
        }
    },
    created() {
        console.log(this.$route)
    },
    methods: {
        onConfirm(value) {
            this.location = value;
            this.showPicker = false;
        },
        formatterFn(value) {
            debugger
            if (!isNaN(value.replace(/[ ]/g, ''))) {
                value = value.replace(/\s/g, '').replace(/(\d{4})(?=\d)/g, '$1 ')
            }
            console.log(value)
            console.log(this.cardNo)
            return value
        }
    }
}
</script>
<style lang="less" scoped>
    .submit-block {
        padding: 0 5vw;
        margin-top: 5vw;
    }
</style>