<template>
    <van-uploader v-model="fileList" :multiple="isMultiple" accept="image/*" :before-read="beforeRead" :after-read="afterRead" />
</template>
<script>
import { Uploader } from 'vant'
export default {
    components: {
        [Uploader.name]: Uploader
    },
    props: {
        isMultiple: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            fileList: []
        }
    },
    methods: {
        beforeRead() {
            // params file
            // 压缩处理。。。。
            return true;
        },
        afterRead(file) {
            debugger
            //uploading 表示上传中，failed 表示上传失败，done 表示上传完成
            const fetch = params => {
                console.log(params.get("file"));
                this.$axios.post('/h5/common/file/upload',params, {
                    loader: false,
                    headers: { "Content-Type": "multipart/form-data" }
                }).then(res => {
                    console.log(res)
                    let url = res.data && res.data.url || '';
                    if (Array.isArray(file)) {
                        file.map(item => {
                            item.status = 'done';
                            item.message = '上传成功';
                        })
                    } else {
                        file.status = 'done';
                        file.message = '上传成功';
                    }
                    this.$emit('get-url', url)
                })
            }
            let params = new FormData(); // 创建form对象
            if (Array.isArray(file)) {
                file.map(item => {
                    item.status = 'uploading';
                    item.message = '上传中...';
                    params.append("file", item.file)
                })
            } else {
                file.status = 'uploading';
                file.message = '上传中...';
                params.append("file", file.file)
            }
            fetch(params)
        },
    }
}
</script>